// IMPORTS
// Material UI
import { Grid } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/core/styles/withStyles'
import DistributionCenterIcon from '@material-ui/icons/Business'
import PropTypes from 'prop-types'

// Externals imports (Dependencies)
import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux'
import { Link, Route } from 'react-router-dom'
import { bindActionCreators } from 'redux'

import styles from '../../../resources/theme/distributionCenters'

// Internal imports
import { fetchDistributionCenters } from '../DistributionCentersActions'
import { getDistributionCenterListData } from '../DistributionCentersReducer'
import EditDistributionCenter from './Edit'
import List from './List'
import NewDistributionCenter from './New'
import Filters from './Filters'
import ListFleets from './fleets/index'

class DistributionCenter extends Component {
  componentDidMount() {
    this.props.fetchDistributionCenters()
  }

  render() {
    const { classes, items } = this.props

    return (
      <Fragment>
        <Grid container justify="center" alignItems="center">
          <Grid item className={classes.grow}>
            <Typography variant="h3">Centros de Distribuição</Typography>
          </Grid>
          <Grid item>
            <Button
              size="medium"
              color="primary"
              variant="contained"
              component={Link}
              to="/distributioncenters/new"
            >
              <DistributionCenterIcon className={classes.btnIcon} /> Cadastrar
            </Button>
          </Grid>
        </Grid>

        <Filters />

        <Route path="/distributioncenters/new" component={NewDistributionCenter} />
        <Route path="/distributioncenters/:id/edit" component={EditDistributionCenter} />
        <Route
          path="/distributioncenters/:id/list-fleets"
          component={ListFleets}
        />

        <List classes={classes} items={items} />
      </Fragment>
    )
  }
}

DistributionCenter.propTypes = {
  classes: PropTypes.object.isRequired,
  fetchDistributionCenters: PropTypes.func.isRequired,
  items: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  ...getDistributionCenterListData(state),
})

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      fetchDistributionCenters,
    },
    dispatch
  )

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(DistributionCenter))
